/*
=============== 
*LAYOUT DEBUGGER - Remove later.
===============
*/
html {
  --outline: 1px dotted rgba(255, 0, 0, 0.5);
  --outline: 0px;
}

* {
  outline: 0px;
  outline: var(--outline);
}

/*
=============== 
*BOX-SIZING
===============
*/
*,
::after,
::before {
  box-sizing: border-box; /** Added by JOHN*/
}

html {
  font-size: 100%;
} /*16px*/

/** =============================================================VARIABLES========================================================= */
:root {
  /*
=============== 
*COLORS
===============
*/

  /* dark shades of primary color*/
  --primary-100: #f6efd7;
  --primary-200: #eedfaf;
  --primary-300: #e5cf87;
  --primary-400: #ddbf5f;
  /* primary/main color */
  --primary-500: #d4af37;
  /* lighter shades of primary color */
  --primary-600: #aa8c2c;
  --primary-700: #7f6921;
  --primary-800: #554616;
  --primary-900: #2a230b;

  /* dark shades of secondary color*/
  --secondary-100: #e6d2e7;
  --secondary-200: #cda4cf;
  --secondary-300: #b477b7;
  --secondary-400: #9b499f;
  /* secondary color */
  --secondary-500: #821c87;
  /* lighter shades of secondary color */
  --secondary-600: #68166c;
  --secondary-700: #4e1151;
  --secondary-800: #340b36;
  --secondary-900: #1a061b;

  /* greys*/
  --grey-50: #f8fafc;
  --grey-100: #d6d6d6;
  --grey-200: #adadad;
  --grey-300: #848484;
  --grey-400: #5b5b5b;
  --grey-500: #323232;
  --grey-600: #282828;
  --grey-700: #1e1e1e;
  --grey-800: #141414;
  --grey-900: #0a0a0a;

  /* accent color */
  --accent: #4cd4b8;
  /* Other utility colors. */
  --black: #222;
  --white: #fff;
  --red-light: #f8d7da; /*!Error messages.*/
  --red-dark: #842029;
  --green-light: #d1e7dd; /**Success messages.*/
  --green-dark: #0f5132;

  /*
=============== 
*FONTS
===============
*/

  --heading-font: "Dosis", sans-serif;
  --body-font: "Titillium Web", sans-serif;
  --small-text: 0.875rem;
  --extra-small-text: 0.7em; /**small text relative to container.*/

  /*
=============== 
*SPACERS
===============
*/
  /**For vertical rhythm.*/
  --spacer-2xs: 0.5rem;
  --spacer-xs: 0.75rem;
  --spacer-s: 1rem;
  --spacer-baseline: 1.5rem;
  --spacer-l: 2rem;
  --spacer-xl: 3rem;
  --spacer-2xl: 4rem;
  --spacer-3xl: 5rem;
  --spacer-4xl: 6rem;
  --spacer-5xl: 7rem;
  --spacer-6xl: 8rem;

  /*
=============== 
*BOX SHADOW
===============
*/
  /** From tailwind.*/
  --shadow-1: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  --shadow-2: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
    0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --shadow-3: 0 10px 15px -3px rgba(0, 0, 0, 0.1),
    0 4px 6px -2px rgba(0, 0, 0, 0.05);
  --shadow-4: 0 20px 25px -5px rgba(0, 0, 0, 0.1),
    0 10px 10px -5px rgba(0, 0, 0, 0.04);

  /*
=============== 
*ESSENTIALS
===============
*/
  /*Layout*/ /*TODO: CUSTOMIZE*/
  --max-width: 1120px; /*Boxed-content*/
  --fixed-width: 600px; /*Fixed-sized content*/
  /*Colors*/ /*TODO: CUSTOMIZE - CHECK ACCESIBILITY*/
  --background-color-light: var(--grey-50);
  --text-color-dark: var(--grey-700);
  --background-color-dark: linear-gradient(
    to top,
    #323232,
    #282828,
    #1f1f1f,
    #161616,
    #0a0a0a
  );
  --text-color-light: var(--grey-100);

  /*Others*/ /*TODO: CUSTOMIZE*/
  --borderRadius: 0.25rem;
  --letterSpacing: 1px;
  --transition: 0.2s ease-in-out all;
}

/** =============================================================BASIC STYLES========================================================= */
body {
  background-image: linear-gradient(
    to right bottom,
    #ffffff,
    #f5f5f5,
    #eaeaea,
    #e0e0e0,
    #d6d6d6
  );
  font-family: "Titillium Web", sans-serif;
  font-family: var(--body-font);
  font-weight: 400;
  line-height: 1.75; /*Usual 1.4-1.6*/
  color: #1e1e1e;
  color: var(--text-color-dark);
  display: flex;
  justify-content: center;
  align-items: center;
}

.contrasting-section {
  background: linear-gradient(
    to top,
    #323232,
    #282828,
    #1f1f1f,
    #161616,
    #0a0a0a
  );
  background: var(--background-color-dark);
  color: #d6d6d6;
  color: var(--text-color-light);
}

p {
  margin-bottom: 1.5rem; /*?ALTERNATIVE: 1rem*/
  max-width: 40em; /*TODO: Added by JOHN*/
}

h1,
h2,
h3,
h4,
h5 {
  margin: 0;
  margin-bottom: 1.38rem; /** margins changed by JOHN*/
  font-family: "Dosis", sans-serif;
  font-family: var(--heading-font);
  font-weight: 400;
  line-height: 1.3;
  text-transform: capitalize; /** Added by JOHN*/
  letter-spacing: 1px;
  letter-spacing: var(--letterSpacing);
}

/*
=============== 
*TYPESCALE
===============
*/
/**SCALE: Major 3rd*/
h1 {
  margin-top: 0;
  font-size: 3.052rem;
}

h2 {
  font-size: 2.441rem;
}

h3 {
  font-size: 1.953rem;
}

h4 {
  font-size: 1.563rem;
}

h5 {
  font-size: 1.25rem;
}

small,
.text-small {
  font-size: 0.875rem;
  font-size: var(--small-text);
}

/* titles */
.title {
  text-align: center;
}

.title-underline {
  background: #d4af37;
  background: var(--primary-500);
  width: 7rem;
  height: 2px;
  margin: 0 auto;
  margin-top: -1rem;
}

/*links*/
a {
  text-decoration: none;
  /*Added by me.*/
  color: #b477b7;
  color: var(--secondary-300);
  transition: 0.2s ease-in-out all;
  transition: var(--transition);
}

a:hover {
  /*Added by me.*/
  color: #e6d2e7;
  color: var(--secondary-100);
}

/*lists*/
ul {
  list-style-type: none;
  padding: 0;
}

/* alerts */
.alert {
  padding: 0.375rem 0.75rem;
  margin-bottom: 1rem;
  border-color: transparent;
  border-radius: 0.25rem;
  border-radius: var(--borderRadius);
}

.alert-danger {
  color: #842029;
  color: var(--red-dark);
  background: #f8d7da;
  background: var(--red-light);
}
.alert-success {
  color: #0f5132;
  color: var(--green-dark);
  background: #d1e7dd;
  background: var(--green-light);
}

/* icons */
.fa-icon::before {
  font-size: 1.5rem;
  color: #d4af37;
  color: var(--primary-500);
}

/*
=============== 
*IMAGES
===============
*/
.img {
  width: 100%; /*container dependant.*/
  display: block; /*To avoid bottom gap.*/
  object-fit: cover;
}

/*
=============== 
*BUTTONS
===============
*/
.btn {
  cursor: pointer;
  color: #fff;
  color: var(--white);
  background: #d4af37;
  background: var(--primary-500);
  border: transparent;
  border-radius: 0.25rem;
  border-radius: var(--borderRadius);
  letter-spacing: 1px;
  letter-spacing: var(--letterSpacing);
  padding: 0.375rem 0.75rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  box-shadow: var(--shadow-1);
  transition: 0.2s ease-in-out all;
  transition: var(--transition);
  text-transform: capitalize;
  display: inline-block; /*To respect vertical margins. - Center w/ parent.*/
}
.btn:hover {
  background: #7f6921;
  background: var(--primary-700);
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1),
    0 4px 6px -2px rgba(0, 0, 0, 0.05);
  box-shadow: var(--shadow-3);
}
.btn-hipster {
  color: #d4af37;
  color: var(--primary-500);
  background: #eedfaf;
  background: var(--primary-200);
}
.btn-hipster:hover {
  color: #eedfaf;
  color: var(--primary-200);
  background: #7f6921;
  background: var(--primary-700);
}
.btn-block {
  width: 100%;
}

/*
=============== 
*FORMS
===============
*/
.form {
  width: 90vw;
  max-width: 600px;
  max-width: var(--fixed-width);
  background: #821c87;
  background: var(--secondary-500);
  border-radius: 0.25rem;
  border-radius: var(--borderRadius);
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
    0 2px 4px -1px rgba(0, 0, 0, 0.06);
  box-shadow: var(--shadow-2);
  padding: 2rem 2.5rem;
  margin: 3rem auto; /*Self-centered. Adjust if in container.*/
}
.form-label {
  display: block;
  font-size: 0.875rem;
  font-size: var(--small-text);
  margin-bottom: 0.5rem;
  text-transform: capitalize;
  letter-spacing: 1px;
  letter-spacing: var(--letterSpacing);
}

.form-input,
.form-textarea {
  width: 100%;
  padding: 0.375rem 0.75rem;
  border-radius: 0.25rem;
  border-radius: var(--borderRadius);
  background: #f8fafc;
  background: var(--background-color-light);
  border: 1px solid #adadad;
  border: 1px solid var(--grey-200);
}

.form-row {
  margin-bottom: 1rem;
}

.form-radio-btn-label,
.form-checkbox-label {
  font-size: 0.875rem;
  font-size: var(--small-text);
  text-transform: capitalize;
  letter-spacing: 1px;
  letter-spacing: var(--letterSpacing);
}

.form-clue {
  font-size: 0.7em;
  font-size: var(--extra-small-text);
}

.form-textarea {
  height: 7rem;
}
::-webkit-input-placeholder {
  font-family: inherit;
  color: #5b5b5b;
  color: var(--grey-400);
}
:-ms-input-placeholder {
  font-family: inherit;
  color: #5b5b5b;
  color: var(--grey-400);
}
::placeholder {
  font-family: inherit;
  color: #5b5b5b;
  color: var(--grey-400);
}
.form-alert {
  color: #842029;
  color: var(--red-dark);
  letter-spacing: 1px;
  letter-spacing: var(--letterSpacing);
  text-transform: capitalize;
}

/*
=============== 
*PRELOADER SPINNER
===============
*/

@-webkit-keyframes spinner {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spinner {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.loading {
  width: 6rem;
  height: 6rem;
  border: 5px solid #5b5b5b;
  border: 5px solid var(--grey-400);
  border-radius: 50%;
  border-top-color: #d4af37;
  border-top-color: var(--primary-500);
  -webkit-animation: spinner 0.6s linear infinite;
          animation: spinner 0.6s linear infinite;
}
.loading {
  margin: 0 auto; /*Self-centered. Adjust if in container.*/
}

/*
========================================================================================================================================
*FIN DE LA TEMPLATE - ESTILOS PARTICULARES
========================================================================================================================================
*/
/*
========================================================================================================================================
*APP.js
========================================================================================================================================
*/
.main-container {
  display: flex;
  grid-gap: 5rem;
  gap: 5rem;
  grid-gap: var(--spacer-3xl);
  gap: var(--spacer-3xl);
  flex-direction: column;
  align-items: center;
}

.main-title {
  margin-top: 100px;
}

/*
========================================================================================================================================
*CARD. js - LISTCONTAINER.js
========================================================================================================================================
*/

.list-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
  grid-gap: 3vw;
  gap: 3vw;
  max-width: 1120px;
  max-width: var(--max-width);
}

.card {
  width: 200px;
  border-radius: 0.25rem;
  border-radius: var(--borderRadius);
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  transition: 0.2s ease-in-out all;
  transition: var(--transition);
  background-image: linear-gradient(
    to top,
    #323232,
    #282828,
    #1f1f1f,
    #161616,
    #0a0a0a
  );
  background-image: var(--background-color-dark);
  display: flex;
  flex-direction: column;
}

.card:hover {
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
}

.card:hover .card-img {
  opacity: 0.5;
}

.card:hover .card-artist-text::after {
  width: 40px;
}

.img-container {
  height: 150px;
  border-radius: 0.25rem;
  border-radius: var(--borderRadius);
}

.card-img {
  width: 100%;
  height: 100%;
  border-top-left-radius: 0.25rem;
  border-top-left-radius: var(--borderRadius);
  border-top-right-radius: 0.25rem;
  border-top-right-radius: var(--borderRadius);
  transition: 0.2s ease-in-out all;
  transition: var(--transition);
}

.card-info-container {
  color: white;
  padding: 1rem;
  padding: var(--spacer-s);
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.card-text-container {
  flex-grow: 1;
}

.card-artist-text::after {
  content: "";
  width: 25px;
  height: 2px;
  display: block;
  background-color: #9b499f;
  background-color: var(--secondary-400);
  margin-bottom: 1rem;
  margin-bottom: var(--spacer-s);
  transition: 0.2s ease-in-out all;
  transition: var(--transition);
}


.progress-container {
  background-color: #282828;
  background-color: var(--grey-600);
  cursor: pointer;
  border-bottom-left-radius: 0.25rem;
  border-bottom-left-radius: var(--borderRadius);
  border-bottom-right-radius: 0.25rem;
  border-bottom-right-radius: var(--borderRadius);
  height: 4px;
  width: 100%;
  overflow: hidden;
}

.progress-line {
  background-color: #fe8daa;
  border-bottom-left-radius: 0.25rem;
  border-bottom-left-radius: var(--borderRadius);
  height: 100%;
  width: 0;
  transition: width 0.1s linear;
}


/*
=============== 
*AUDIO PLAYER BUTTON
===============
*/
.audio-details-container {
  display: flex;
  justify-content: space-between;
}

.audio-player {
  color: #d4af37;
  color: var(--primary-500);
  font-size: 2rem;
  cursor: pointer;
  transition: 0.2s ease-in-out all;
  transition: var(--transition);
}

.audio-player:hover {
  -webkit-transform: scale(1.15);
          transform: scale(1.15);
}

/*
========================================================================================================================================
*CONTACT.js
========================================================================================================================================
*/

.contact-section {
  max-width: 1120px;
  max-width: var(--max-width);
  padding: 4rem;
  padding: var(--spacer-2xl);
  display: flex;
  justify-content: center;
  border-radius: 0.25rem;
  border-radius: var(--borderRadius);
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1),
    0 10px 10px -5px rgba(0, 0, 0, 0.04);
  box-shadow: var(--shadow-4);
  background-image: linear-gradient(
    to top,
    #323232,
    #282828,
    #1f1f1f,
    #161616,
    #0a0a0a
  );
  background-image: var(--background-color-dark);
  color: #d6d6d6;
  color: var(--text-color-light);
}

.contact-info-container {
  max-width: 60%;
  display: flex;
}

.contact-text-container h2::after {
  content: "";
  width: 25px;
  height: 2px;
  display: block;
  background-color: #9b499f;
  background-color: var(--secondary-400);
  margin-bottom: 1rem;
  margin-bottom: var(--spacer-s);
  transition: 0.2s ease-in-out all;
  transition: var(--transition);
}

.contact-text-container:hover h2::after {
  width: 40px;
}

.job {
  color: #e5cf87;
  color: var(--primary-300);
}

.contact-links-container {
  margin-bottom: 2rem;
  margin-bottom: var(--spacer-l);
}

.contact-links-container a {
  display: block;
}

.icons-container::after {
  content: "";
  width: 15px;
  height: 2px;
  display: block;
  background-color: #e5cf87;
  background-color: var(--primary-300);
  margin-bottom: 1rem;
  margin-bottom: var(--spacer-s);
}

.contact-icon {
  font-size: 1.3rem;
  margin-right: 1rem;
  margin-right: var(--spacer-s);
}

.profile-img-container {
  display: flex;
  align-items: center;
  margin-left: 4rem;
  margin-left: var(--spacer-2xl);
}

.profile-img {
  border-radius: 50%;
  width: 100%;
}

/*
========================================================================================================================================
*FAQ.js
========================================================================================================================================
*/
.faq-title {
  display: flex;
  align-items: center;
  justify-content: center;
}

.questions-container {
  width: 1120px;
  width: var(--max-width);
  border-radius: 0.25rem;
  border-radius: var(--borderRadius);
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  display: grid;
  grid-template-columns: 20% 1fr;
}

.question header {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  cursor: pointer;
}

.question {
  border-radius: 5px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  margin: 2%;
  padding: 2%;
}

.question h5::after {
  content: "";
  width: 15px;
  height: 2px;
  display: block;
  background-color: #9b499f;
  background-color: var(--secondary-400);
  margin-bottom: 1rem;
  margin-bottom: var(--spacer-s);
  transition: 0.2s ease-in-out all;
  transition: var(--transition);
}

.question header:hover h5::after {
  width: 40px;
}

.minus-plus-btn {
  border-radius: 50%;
  border-color: transparent;
  width: 2rem;
  height: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  background-color: #eae6eb;
  color: tomato;
}

.answer {
  text-align: justify;
}

/*
========================================================================================================================================
*DETALLECANCION.js
========================================================================================================================================
*/
.detail-container {
  padding: 3vw;
  margin: 3vw;
  width: 80vw;
  height: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-content: center;
  border-radius: 5px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.detail-data {
  font-style: italic;
}

.detail-image {
  border-radius: 5px;
}

