}

.angry-badge {
  background-color: red;
}

.calm-badge {
  background-color: green;
}

.inspirational-badge {
  background-color: moccasin;
}

.bright-badge {
  background-color: papayawhip;
}

.dark-badge {
  background-color: gray;
}

.funky-badge {
  background-color: peru;
}

/*
